
.story-desk{
    margin-bottom: 50px;
}

.story-desk__list{
    display: flex;
    justify-content: space-between;
}

.story-desk__item{
    border:1px solid #ee9ca7;
    box-shadow: 0 0 10px #ee9ca7;
    width: 30%;
    position: relative;
    text-align: center;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 200px;

    border-radius: 4px;
    overflow: hidden;
}

.story-desk__img{
    position: absolute;
    top: 0;
    width: 30%;
    max-height: 100%;
}

.story-desk__img--1{
    right: 0;
}

.story-desk__img--2{
    right: 0;
    bottom: -4px;
    top: inherit;
}

.story-desk__img--3{
    left: 0;
}

.story-desk__img-pic{
    max-width: 100%;
    max-height: 100%;
}

.story-desk__name{
    font-weight: 400;
    font-size: 25px;
}

.story-desk__count{
    font-size: 20px;
    font-weight: 300;
}

.story-desk__icon{
    width: 70px;
    height: 70px;
}

.story-desk__icon-pic{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

@media screen and (max-width:768px){
    .story-desk__list{
        flex-wrap: wrap;
        justify-content: center;
        margin-right: -20px;
        margin-bottom: -20px;
        width: 100%;
    }

    .story-desk__item{
        width: 48%;
        margin-right: 20px;
        margin-bottom: 20px;
    }

    .story-desk__item:nth-child(2){
        margin-right: 0;
    }

    .story-desk__item:last-child{
        margin-right: 0;
    }
}

@media screen and (max-width:600px){
    .story-desk__list{
        margin-right: 0;
    }

    .story-desk__item{
        width: 100%;
        margin-right: 0;
    }
}

@media screen and (max-width:320px){
    .story-desk__item{
        height: 170px;
    }
}