/* .form{} */

.form__label{
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.form__name{
    margin-bottom: 10px;
    color: #666;
}

.form__input{
    border: 1px solid #e1e1e1;
    outline: none;
    border-radius: 3px;
    padding: 10px 15px;
    line-height: 1.45;
    color: #404040;
    background-color: #fff;
}

.form__textarea{
    resize:vertical;
    overflow: hidden;
}

.form__info{
    color: #666;
    font-weight: 400;
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid rgba(0,0,0,.1);
}

.form__btn{
    position: relative;
}