.gallery{
    height: 100%;
    width: 100%;
}

.gallery .content__wrapp{
    overflow-x: visible;
}

.gallery__list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    perspective: 1500px;
    margin-bottom: -20px;
}

.gallery__item{
    width: 30%;
    height: 200px;
    transition: .1s;
    position: relative;
    box-shadow: 0 0 10px black;
    margin-bottom: 20px;
}

.gallery__item:before{
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
    background: rgba(20, 58, 84, .6);
}

.gallery__link{
    text-decoration: none;
    color: #fff;
    font-family: 'Kaushan Script', cursive;
    font-size: 35px;

    z-index: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.gallery__img{
    height: 100%;
    width: 100%;
    position: absolute;
    top:0;
    left: 0;
    /* /* background-color:#fff;* */
}

.gallery__img-pic{
    max-height: 100%;
    height: 100%;
    width: 100%;
    object-fit: cover;
}

@media screen and (max-width:768px){
    .gallery__item{
        width: 45%;
    }
}

@media screen and (max-width:480px){
    .gallery__item{
        width: 100%;
    }
}