.page{
    transition-delay: 2s  !important;
    transition: opacity 2s !important;
    min-height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: .3s;
    background-color: white;    
}

.page__wrapp{
    height: 100%;
    width: 100%;
}


.page__bar{
    width: 25%;
    position: fixed;
    right: 0;
    top:0;
    height: 100vh;
    background: #bc4e9c;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #f80759, #bc4e9c);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #f80759, #bc4e9c); 
}

.page__name{
    transform-origin: 0 0;
    transform: rotate(90deg);
    color: white;
    position: absolute;
    top: 55%;
    left: 50%;
    font-size: 90px;
    font-weight: 600;
}

.page__link{
    width: 100%;
    height: 100%;
    display: block;
}

.page__cross{
    position: absolute;
    z-index: 1;
    top:10px;
    right: 20px;
    height: 30px;
    width: 30px;
    display: inline-block;
    transition: .2s;
}

.page__cross:hover{
    transform: scale(1.2);
}

.page__cross:before, .page__cross:after{
    content:'';
    display: inline-block;
    width: 100%;
    height: 2px;
    background-color: #ffffff;
    position: absolute;
    top:50%;
    left: 0;
    transform: translateY(-50%);
    transform-origin: 50% 50%;
}

.page__cross:before{
    transform: rotate(45deg);
}

.page__cross:after{
    transform: rotate(-45deg);
}

.vision{
    transition-delay: 2s;
    opacity: 1;
    overflow: visible;
    pointer-events:all;
}


@media screen and (max-width:900px){
    .page__bar{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 10vh;
    }

    .page__name{
        transform: none;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: 60px;
    }

    .page__cross{
        height: 40px;
        width: 40px;
        border-radius: 50%;
        background: #fff;
        box-shadow: 0 0.125rem 0.25rem rgba(0,0,0,0.2);
        top: 35px;
        position: fixed;
        display: flex;
        justify-content: center;
    }

    .page__cross:after, .page__cross:before{
        background: black;
        width: 25px;
        left: inherit;
        border-radius: 20px;
    }
}