.accordeon{
    display: inline-block;
    width: 50%;
}

.accordeon__item{
    margin-bottom: 15px;
}

.accordeon__item:last-child{
    margin-bottom: 0;
}

.accordeon__descr{
    overflow: hidden;
    height: 0;
}

.accordeon__title{
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center
}

.accordeon__triangle{
    transition: all .3s;
    display: inline-block;
    border: 8px solid transparent;	
    border-bottom: 8px solid grey;
}

.active{
    overflow: visible;
    height: 100%;
}

@media screen and (max-width:768px){
    .accordeon{
        width: 100%;
    }
}

