.about{
    margin-right: 30px;
}

.about, .skills{
    flex: 1;
}

.about__item{
    margin-bottom: 30px;
    display: flex;
    align-items: center;
}

.about__item:last-child{
    margin-bottom: 0;
}

.about__name{
    margin-right: 10px;
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 20px;
}

.about__info{
    color: #727272;
}

.about__title, .skills__title{
    font-weight: 500;
    font-size: 25px;
    margin-bottom: 20px;
    padding-bottom: 15px;
    border-bottom: 1px solid rgba(0,0,0,.1);
}

@media screen and (max-width:768px){
    .about{
        margin-right: 0;
        margin-bottom: 25px;
        width: 100%;
    } 
}


@media screen and (max-width:320px){
    .about__item{
        flex-direction: column;
    } 

    .about__name{
        margin-right: 0;
        margin-bottom: 10px;
    }
}