@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700);
@import url(https://fonts.googleapis.com/css?family=Kaushan+Script&display=swap);
@import url(https://fonts.googleapis.com/css?family=Monoton&display=swap);
@import url(https://fonts.googleapis.com/css?family=Gloria+Hallelujah&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap);

*{
  box-sizing: border-box;
}

body,html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;

  font-family: 'Roboto', sans-serif;
}

ul,li{
  list-style: none;
  padding: 0;
  margin: 0;
}

.none{
  display: none;
}

.button{
  outline: none;
  border: 1px solid transparent;
  cursor: pointer;
  font-family: 'Monoton', cursive;
  box-shadow: 0 0 55px blue;
  font-size: 27px;
  padding: 2px 10px;
  position: relative;
  -webkit-transition: .4s;
  transition: .4s;
}

.button:hover{
  border-color: blue;
}

.button::before , .button::after{
  content:'';
  display: inline-block;
  height: 20px;
  width: 20px;
  background-color: #0f2344;
  position: absolute;
  z-index: -1;
}

.button::before{
  top: -5px;
  left: -9px;
}

.button::after{
  bottom: -5px;
  right: -9px;
}

.btn{
  outline: none;
  border: 1px solid transparent;
  padding: 5px 20px;
  background-color: #f37f29;
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 15px;
  border-radius: 4px;
  cursor: pointer;
  letter-spacing: 1px;
  padding: 11px 20px;
  width: 100%;
  -webkit-transition: .4s;
  transition: .4s;
}

.btn:hover{
  background-color: #d8762d;
}

.content{
  width: 75%;
  height: 100%;
  padding: 35px;
  box-sizing: border-box;
  /* overflow-x: hidden; */
  
}

.content__wrapper{
  display: flex;
}

.modal{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  height: 200px;
  width: 200px;
  background-color:  black;
  z-index: 20;
}

@media screen and (max-width:900px){
  .content{
    width: 100%;
  }

  .content__wrapp{
    margin-top: 60px;
    overflow-x: hidden;
  }
}

@media screen and (max-width:768px){
  .content__wrapper{
    flex-direction: column;
    flex-wrap: wrap;
  }
}


#root{
  width: 100%;
  height: 100%;
}

.display-left, .display-right{
  height: 100%;

  position: absolute;
  z-index: 10;
  -webkit-transition: all .5s;
  transition: all .5s;
}

.display-left{
  left: 0;
  width: 65%;
  background: #edeff2;

  display: flex;
  align-items: center;
  justify-content: center;
}

.display-right{
  width: 35%;
}

.display-right{
  right: 0;
  background-color: black;
}

@media screen and (max-width:768px){
