.between{
    position: absolute;
    background-color: transparent;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    z-index: 3;
    overflow: hidden;
    pointer-events: none;
}

.block{
    position: absolute;
    height: 100%;
    background: #DA4453;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #89216B, #DA4453);  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, from(#89216B), to(#DA4453));
    background: -webkit-linear-gradient(top, #89216B, #DA4453);
    background: linear-gradient(to bottom, #89216B, #DA4453); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    

    -webkit-transition:all .3s;
    

    transition:all .3s;
    /* transition-delay: .2s; */
    width: 0;
}

.b1{
    left: 0;
}

.b2{
    left: 25%;
}

.b3{
    left: 50%;
}

.b4{
    left: 75%;
}


.blocks{
    -webkit-animation: blocks 1s normal;
            animation: blocks 1s normal;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}


@-webkit-keyframes blocks{
    from{
        width: 0;
    }

    50%{
        width: 25%;
        -webkit-transition-delay: .2s;
                transition-delay: .2s;
    }
    to{
        width: 0;
    }
}


@keyframes blocks{
    from{
        width: 0;
    }

    50%{
        width: 25%;
        -webkit-transition-delay: .2s;
                transition-delay: .2s;
    }
    to{
        width: 0;
    }
}




.preloader{
    position: fixed;
    top: 0;
    left: 0;
	width: 100%;
	height: 100vh;
	z-index: 20;
}

.preloader__block{
	position: absolute;
	height: 100%;
	width: 50%;
	top:0;
	left: 0;
	background-color: #000000;
