.preloader{
    position: fixed;
    top: 0;
    left: 0;
	width: 100%;
	height: 100vh;
	z-index: 20;
}

.preloader__block{
	position: absolute;
	height: 100%;
	width: 50%;
	top:0;
	left: 0;
	background-color: #000000;
	transition: .4s;
}

.preloader__block--second{
	left:50%;
}

.tri {
	position: absolute;
    top: 62%;
    left: 44%;
    transform: translate(-50%, -50%);
	width: 50px;
	height: 50px;
}

.tri .side {
	width: 50px;
	height: 50px;
	position: absolute;
	transform-origin: center center;
}

.tri .side:nth-child(1) {
	transform: translate(-6px, -151px) rotate(53deg);
    z-index: 2;
}

.tri .side:nth-child(2) {
	transform: translateY(-150px);
}

.tri .side:nth-child(3){
	transform: translate(52px, -67px) rotate(69deg);
}

.tri .ring {
	width: 50px;
	height: 50px;
	position: absolute;
	background: white;
	animation-name: rotate, cromatic;
	animation-duration: 3s, 3s;
	animation-timing-function: linear, linear;
	animation-iteration-count: infinite, infinite;
	opacity: 0.5;
	box-shadow: 0 0 30px black;
	border-radius: 20%;
}

.side:nth-child(1) .ring:nth-child(0) {
	margin: 0 0 0 0px;
	z-index: 0;
	animation-delay: 0s;
}

