@keyframes rotate {
	to {
		transform: rotate(360deg);
	}
}

@keyframes cromatic {
	0% {
		background-color: #FFFF00;
	}

	15% {
		background-color: #008F59;
	}

	30% {
		background-color: #2771B2;
	}

	45% {
		background-color: #6E3795;
	}

	60% {
		background-color: #FF0000;
	}

	75% {
		background-color: #FF9201;
	}

	100% {
		background-color: #FFFF00;
	}
}

.left{
