.between{
    position: absolute;
    background-color: transparent;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    z-index: 3;
    overflow: hidden;
    pointer-events: none;
}

.block{
    position: absolute;
    height: 100%;
    background: #DA4453;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #89216B, #DA4453);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #89216B, #DA4453); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    

    transition:all .3s;
    /* transition-delay: .2s; */
    width: 0;
}

.b1{
    left: 0;
}

.b2{
    left: 25%;
}

.b3{
    left: 50%;
}

.b4{
    left: 75%;
}


.blocks{
    animation: blocks 1s normal;
    animation-direction: alternate;
    animation-fill-mode: forwards;
}


@keyframes blocks{
    from{
        width: 0;
    }

    50%{
        width: 25%;
        transition-delay: .2s;
    }
    to{
        width: 0;
    }
}



