.contact{
    display: flex;  
}

.contact__title{
    font-weight: 500;
    font-size: 22px;
    margin-bottom: 25px;
}

.contact__col{
    flex: 1;
    margin-right: 30px;
}

.contact__col:last-child{
    margin-right: 0;
}

.contact__info{
    color: #666;
    font-weight: 400;
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid rgba(0,0,0,.1);
}

@media screen and (max-width:480px){
    .contact{
        flex-wrap: wrap;
        flex-direction: column-reverse;
    }

    .contact__col{
        flex-basis: 100%;
        margin-right: 0;
    }

    .contact__col:last-child{
        margin-bottom: 25px;
    }
}