.services__list{
    display: flex;
    margin-bottom: 25px;
}

.services__item{
    flex: 1;
    margin-right: 30px;
}

.services__item:last-child{
    margin-right: 0;
}

.services__icon{
    width: 50px;
    height: 50px;
    display: inline-block;
    margin-bottom: 15px;
}

.services__icon-pic{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

@media screen and (max-width:768px){
    .services__list{
        flex-wrap: wrap;
        flex-direction: column;
    }

    .services__item{
        flex-basis: 100%;
        margin-right: 0;
        margin-bottom: 20px;
    }

    .services__item:last-child{
        margin-bottom: 0;
    }
}

