
.skills__item{
    margin-bottom: 38px;
}

.skills__item:last-child{
    margin-bottom: 0;
}

.skills__name{
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 18px;
}

.skills__experience{
    color: #727272;
    margin-top: 10px;
}

.skills__line{
    height: 2px;
    background: #ddd;
    width: 100%;
    position: relative;
    margin-top: 13px;
}

.skills__line-over{
    height: 100%;
    background: #666;
}

.skills__line-count{
    position: absolute;
    bottom: 10px;
    right: 0;
    font-size: 16px;
    color: black;
}