#root{
  width: 100%;
  height: 100%;
}

.display-left, .display-right{
  height: 100%;

  position: absolute;
  z-index: 10;
  transition: all .5s;
}

.display-left{
  left: 0;
  width: 65%;
  background: #edeff2;

  display: flex;
  align-items: center;
  justify-content: center;
}

.display-right{
  width: 35%;
}

.display-right{
  right: 0;
  background-color: black;
}

@media screen and (max-width:768px){
  .display-left, .display-right{
    width: 100%;
  }

  .display-left{
    height: 70vh;
  }

  .display-right{
    height: 30vh;
    bottom: 0;
  }
}
