.parallax{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 1;

    display: flex;
    align-items: center;
    justify-content: center;

    perspective: 1500px;
    perspective-origin: 50% 50%;
    transform-style: preserve-3d;

    transition:all .7s;
    transition-timing-function:linear;
}

.parallax__img{
    max-width: 90%;
    max-height: 90%;
    transition: .7s;
    transition-timing-function:linear;
}

@media screen and (max-width:480px){
    .parallax__img{
        max-width: 100%;
        max-height: 100%;
    }
}