.menu, .menu__list{
    width: 100%;
    height: 100%;
}

.menu__list{
    display: flex;
    flex-wrap: wrap;
}

.menu__item{
    height: 50%;
    width: 50%;
    position: relative;
    background-size: cover;
    overflow: hidden;

    cursor: pointer;
}

.menu__item:hover .menu__bg{
    animation: transfrom .5s ease-in-out forwards;
    animation-direction: alternate;
}

.menu__link{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    text-decoration: none;
}

.menu__bg{
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    min-height: 100%;
    background-size: cover;
    background-position: 50% 0%;
    background-repeat: no-repeat;
}

.menu__bg--about{
    background-image: url(../../assets/menu/about.jpg);
}

.menu__bg--portfolio{
    background: url(../../assets/menu/portfolio.jpg);
}

.menu__bg--services{
    background: url(../../assets/menu/service.jpg);
    background-position: 50% 50%;
}

.menu__bg--contact{
    background: url(../../assets/menu/gallery.jpg);
}  

.menu__name{
  color: #fff;
  font-weight: 700;
  font-size: 32px;
  z-index: 2;
}


@keyframes transfrom{
    from{
        transform: scale(1);
    }
    to{
        transform: scale(1.1);
    }
}