.fallout{
    width: 300px;
    box-shadow: 0 0 20px #f37f29;
    display: inline-block;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px;
    margin-top: 35px;
    background: #ffffff;
}

.fallout:before{
    content: '';
    display: inline-block;
    border: 20px solid transparent;	
    border-bottom: 20px solid #f37f29;
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    margin-bottom: 3px;
}

.fallout__text{
    text-align: center;
    font-size: 18px;
}

@media screen and (max-width:480px){
    .fallout{
        bottom: 100%;
        top: inherit;
        margin-top: 0;
        margin-bottom: 35px;
    }

    .fallout:before{
        top: 100%;
        bottom: inherit;
        margin-bottom: 0;
        margin-top: 3px;
        border-bottom: none;
        border-top: 20px solid #f37f29;
    }
}