.info{
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 5;
  transform: translate(-50%,-50%);
  font-family: 'Monoton', cursive;
  color: black;
  font-size: 48px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 3px;
  background: #fff;
  box-shadow: 0 0 75px blue;
  padding: 17px;
}

.info__name{
  line-height: 60px;
}

.info__position{
  font-size: 29px;
  margin-top: 9px;
}

@media screen and (max-width:480px){
  .info{
    font-size: 38px;
  }

  .info__position{
    font-size: 25px;
  }

}


