/* .socials{} */

.socials__list{
    display: flex;
    align-items: center;
}

.socials__item{
    margin-right: 15px;
}

.socials__item:last-child{
    margin-right: 0;
}

.socials__link{
    display: inline-block;
    width: 25px;
    height: 25px;
    border:1px solid transparent;
    border-radius: 50%;
    background: #e0eafc; 

    position: relative;
    transition: .4s;
}

.socials__link:hover{
    background: #bcc5d6;
}

.socials__img{
    position: absolute;    
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    height: 15px;
    width: 15px;
}